html {
  font-size: 14px;
}

body, html {
  font-family: "Raleway", sans-serif;
  margin: 0;
  padding: 0;
  width: 100%;
  max-width: 100%;
  overflow-x: hidden;
  min-height: 100vh;
}

.container {
  margin: 0 auto;
}

h1, h2, h3 {
  font-weight: 700;
}

h1 {
  font-size: 2.827rem;
}

h2 {
  font-size: 1.999rem;
}

h3 {
  font-size: 1.414rem;
}

a, a:visited {
  color: #2196F3;
  font-weight: 500;
  text-decoration: none;
}

@media only screen and (min-device-width: 768px) and (max-device-width: 1024px) and (-webkit-min-device-pixel-ratio: 1) {
  html {
    font-size: 15px;
  }
}

/* ----------- Non-Retina Screens ----------- */
@media screen and (min-device-width: 1024px) and (max-device-width: 1600px) and (-webkit-min-device-pixel-ratio: 1) {
  html {
    font-size: 16px;
  }
}

/* ----------- Retina Screens ----------- */
input {
  outline: none;
  font-family: "Raleway", sans-serif;
}

.welcome {
  height: 70vh;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-direction: normal;
  -webkit-box-orient: vertical;
  -webkit-flex-direction: column;
  -moz-flex-direction: column;
  -ms-flex-direction: column;
  flex-direction: column;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  -webkit-justify-content: center;
  -moz-justify-content: center;
  justify-content: center;
}

.welcome-header {
  font-size: 3.998rem;
  margin: 0;
}

.welcome-paragraph {
  font-size: 1.414rem;
  width: 40rem;
}

.welcome-search .search-input {
  padding: 1rem 2rem;
  font-size: 1.414rem;
  width: 60%;
}

.welcome-links {
  margin-top: .5rem;
}

.btn {
  padding: 1rem 2rem;
  border: 1px solid black;
  border-radius: .2rem;
  font-size: 1.414rem;
  background: transparent;
  transition: all .2s ease-in;
}

.btn:hover {
  background: black;
  color: white;
}

header {
  background: transparent;
  padding: 1.5rem 0;
  border-bottom: 1px solid #eee;
}

header .row {
  margin: 0 auto;
}

header .header-logo {
  font-size: 2rem;
  font-weight: 500;
}

header .header-logo p {
  margin: 0;
}

header .header-right {
  text-align: right;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
  -ms-flex-align: center;
  -webkit-align-items: center;
  -moz-align-items: center;
  align-items: center;
  -webkit-box-pack: end;
  -ms-flex-pack: end;
  -webkit-justify-content: flex-end;
  -moz-justify-content: flex-end;
  justify-content: flex-end;
}

header .header-right ul.menu {
  list-style: none;
  margin: 0;
}

footer.footer {
  padding: .7rem;
  text-align: center;
  background-color: #0e4377;
  color: white;
  bottom: 0;
  width: 100%;
  display: -webkit-box;
  display: -webkit-flex;
  display: -moz-flex;
  display: -ms-flexbox;
  display: flex;
  -webkit-align-self: flex-end;
  -moz-align-self: flex-end;
  -ms-flex-item-align: end;
  align-self: flex-end;
}

